.product-item {
  width: 300px;

  text-align: center;
  margin: 0 auto;
  border: 1px solid;
  background: white;
  font-family: "Open Sans";
  transition: .3s ease-in;
}
.product-item img {
  display: block;
  width: 100%;
}
.product-list {
  background: #FAFAE1;
  padding: 15px 0;
}
.product-list h3 {
  font-size: 18px;
  font-weight: 400;
  color: #444444;
  margin: 0 0 10px 0;
}
.price {
  font-size: 16px;
  color: #fc5a5a;
  display: block;
  margin-bottom: 12px;
}
.product-list button {
  text-decoration: none;
  display: inline-block;
  padding: 0 12px;
  background: #cccccc;
  color: white;
  text-transform: uppercase;
  font-size: 12px;
  line-height: 28px;
  transition: .2s ease-in;
  border-radius: 4px;
}
.product-item:hover button {
  background: #fc5a5a;
}